      fullStyle: ['/*\n* Inspired by http://strml.net/\n* \u5F00\u6E90\u5730\u5740\uFF1Ahttps://github.com/orzxlee/resume\n* \u5927\u5BB6\u597D\uFF0C\u6211\u662FOrz\u3002\n* \u6211\u6765\u5199\u4E00\u4EFD\u7B80\u5386\uFF01\n*/\n\n/* \u7ED9\u6240\u6709\u5143\u7D20\u52A0\u4E0A\u8FC7\u6E21\u6548\u679C */\n* {\n  transition: all .2s;\n}\n/* \u8BBE\u7F6E\u80CC\u666F\u989C\u8272 */\nhtml {\n  color: rgb(222,222,222);\n  background: rgb(63,82,99);\n}\n/* \u8BBE\u7F6E\u8FB9\u6846 */\n.styleEditor {\n  padding: .5em;\n  border: 1px solid;\n  overflow: auto;\n  width: 90vw;\n  margin: 2.5vh 5vw;\n  height: 90vh;\n}\n/* \u592A\u9AD8\u4E86 */\n.styleEditor {\n  height: 45vh;\n}\n/* \u4EE3\u7801\u9AD8\u4EAE */\n.token.selector{\n  color: rgb(133,153,0);\n}\n.token.property{\n  color: rgb(230,159,15);\n}\n.token.punctuation{\n  color: yellow;\n}\n.token.function{\n  color: rgb(100,200,200);\n}\n\n/* \u52A03D\u6548\u679C */\nhtml{\n  perspective: 1000px;\n}\n.styleEditor {\n  position: fixed; left: 0; top: 0;\n  transform: rotateX(-10deg) translateZ(-50px) ;\n}\n\n/* \u51C6\u5907\u4E00\u4E2A\u7F16\u8F91\u5668 */\n.resumeEditor{\n  position: fixed;\n  top: 50%; left: 0;\n  padding: .5em;  margin: 2.5vh;\n  width: 95vw; height: 45vh;\n  border: 1px solid;\n  background: white; color: #222;\n  overflow: auto;\n}\n/* \u5F00\u59CB\u5199\u7B80\u5386 */\n\n\n', '\n/*\u5C06Markdown\u683C\u5F0F\u7FFB\u8BD1\u6210HTML\n *\u518D\u5BF9HTML\u52A0\u70B9\u6837\u5F0F\n*/\n', '\n.resumeEditor{\n  padding: 2em;\n}\n.resumeEditor h2{\n  display: inline-block;\n  border-bottom: 1px solid;\n  margin: 1em 0 .5em;\n}\n.resumeEditor ul,.resumeEditor ol{\n  list-style: none;\n}\n.resumeEditor ul> li::before{\n  content: \'\u2022\';\n  margin-right: .5em;\n}\n.resumeEditor ol {\n  counter-reset: section;\n}\n.resumeEditor ol li::before {\n  counter-increment: section;\n  content: counters(section, ".") " ";\n  margin-right: .5em;\n}\n.resumeEditor blockquote {\n  margin: 1em;\n  padding: .5em;\n  background: #ddd;\n}\n'],
      currentMarkdown: '',
      fullMarkdown: 'orzxlee\n====\n\u5750\u6807\uFF1A\u9ED1\u9F99\u6C5F\u54C8\u5C14\u6EE8\u3002\n\n\u6C42\u804C\u610F\u5411\uFF1AJava\u5DE5\u7A0B\u5E08\n\n\u6280\u80FD\n----\n  - \u719F\u6089Spring/SpringMVC/Mybatis\u7B49\u5F00\u6E90\u6846\u67B6\u53CA\u6CE8\u89E3\u5F0F\u5F00\u53D1\u3002\n  - \u719F\u6089JDBC\u3001Ajax\u3001Xml\u3001Servlet\u7B49\u76F8\u5173\u6280\u672F\u3002\n  - \u719F\u7EC3\u8FD0\u7528Jsp\u3001Css\u3001HTML\u3001Jquery\u7B49\u76F8\u5173\u6280\u672F\u8FDB\u884C\u9879\u76EE\u524D\u7AEF\u7684\u5F00\u53D1\u3002\n  - \u719F\u7EC3\u4F7F\u7528Oracle\u3001Mysql\u7B49\u4E3B\u6D41\u6570\u636E\u5E93\u6765\u8FDB\u884C\u7CFB\u7EDF\u5F00\u53D1\u3002\n  - \u719F\u7EC3\u8FD0\u7528Tomcat\u7B49\u670D\u52A1\u5668\u8FDB\u884C\u9879\u76EE\u7684\u5F00\u53D1\u3002\n  - \u719F\u7EC3\u8FD0\u7528Eclipse\u3001MyEclipse\u7B49\u5F00\u53D1\u5DE5\u5177\u3002\n  - \u4F7F\u7528\u8FC7Hibernate\u3001Dubbox\u3001Zookeeper\u3001FastDFS\u3001SpringDataRedis\u3001SpringSecurity\u3001SpringDataSolr \n  - \u8BFB\u8FC7Spring\u5E95\u5C42\u6E90\u7801\uFF0C\u624B\u5199\u8FC7SpringMVC\u6846\u67B6\n\n\u540E\u7AEF\u5F00\u53D1\n----\n  - \u7528\u6237\u7BA1\u7406\n  - \u5355\u70B9\u767B\u5F55\n  - \u7B2C\u4E09\u65B9\u767B\u5F55\n  - \u6743\u9650\u7BA1\u7406\n  - \u652F\u4ED8\u7CFB\u7EDF\n\n\u9879\u76EE\u7ECF\u9A8C\n---- \n  - \u8FDB\u9500\u5B58\u7BA1\u7406\u7CFB\u7EDF\n  - \u7A0E\u52A1\u5C40\u534F\u540C\u529E\u516C\u5E73\u53F0\n  - \u9A7E\u6821\u4EBA\u5458\u7BA1\u7406\u7CFB\u7EDF\n  - \u5B66\u751F\u4FE1\u606F\u7BA1\u7406\u7CFB\u7EDF\n  - \u7535\u5546\u7CFB\u7EDF\n\n\u6280\u672F\u53CA\u8BED\u8A00\n----\n  - Java: SpringMVC, Hibernate, iBatis\n  - Node.js: express\n  - DotNet and PHP\n  - DB: SQLServer, Oracle, MySQL/MariaDB, redis\n  - WebServer: apache, tomcat, netty\n  - OS: Ubuntu, CentOS, Windows\n  - Others: Docker, git, Xmind, Axure\n\n\u5B9E\u4E60\u7ECF\u5386\n----\n\n  - \u54C8\u5C14\u6EE8\u4E2D\u8F6F\u777F\u8FBE\u4FE1\u606F\u6280\u672F\u516C\u53F8 \u540E\u53F0\u7BA1\u7406\u7CFB\u7EDF\u9879\u76EE\n\n\u6559\u80B2\u7ECF\u5386\n----\n\n - \u54C8\u5C14\u6EE8\u8FDC\u4E1C\u7406\u5DE5\u5B66\u9662 \u8F6F\u4EF6\u5DE5\u7A0B\u5B66\u58EB\n\n\u94FE\u63A5\n----\n\n* [GitHub](https://github.com/orzxlee)\n* [\u6280\u672F\u535A\u5BA2](https://blog.csdn.net/orzxlee)\n\n\u52FE\u5F15\u65B9\u5F0F\n----\n\n* \u5FAE\u4FE1\uFF1Aorzxlee\n\u9E23\u8C22\n----\n\n*  \u90AE\u7BB1\uFF1Aorzxlee@qq.com\n*  2019\u5E7410\u670810\u65E5\n\n'
